@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;700&display=swap");

html {
    min-height: 100%;
    position: relative;
    overflow: scroll;
}

body {
    font-family: "Quicksand", sans-serif;
    color: #666;
}
.carritou {
    width: 36px;
    position: absolute;
    right: 24px;
    top: 50px;
}
.volver {
    width: 20px;
    position: absolute;
    left: 24px;
    top: 58px;
}
.titulo {
    text-align: center;
    font-weight: 700;
    margin-top: 0px;
}
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    /* height: 100vh;*/
    width: 100%;
}
.wraper {
    display: block;
    width: 355px;
    height: 90vh;
}
.home {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 90vh;
    overflow: scroll;
}

.carrito-number {
    color: white;
    font-weight: bold;
    background-color: rgb(246, 84, 111);
    border-radius: 100%;
    width: 26px;
    height: 26px;
    position: absolute;
    top: 42px;
    right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

header {
    margin-top: 40px;
    margin-bottom: 20px;
}

footer {
    text-align: center;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 50px; 
    background-color: white;
    z-index: 1;
}

footer hr {
    color: #666;
    width: 75%;
}

footer h4 {
    margin: 0;
}

.footer1 {
    text-align: center;
}