
.carrito {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.carrito-listadito {
    width: 100%;
    height: 50vh;
    border: 1px solid #333;
    border-radius: 10px;
    overflow: scroll;
}

.carrito-boton {
    margin-top:20px
}

button {
    margin: 0px;
    background: #2f39ff;
    color: white;
    font-size: 16px;
    border: 0px;
    border-radius: 10px;
    width: 90px;
    height: 35px;
    float: right;
}